import React from "react";
import ReactDOM from "react-dom";
import { browserSupportsBackdropFilter } from "./browserSupportsBackdropFilter";

export default domRender;

async function domRender(goldpage_args) {
  await goldpage_args.page.on_page_load(goldpage_args);
  if (browserSupportsBackdropFilter()) {
    document.documentElement.classList.add("backdrop-filter-support");
  }
  if (goldpage_args.page.hydrateReact) {
    hydrateReact(goldpage_args);
  }
}

function hydrateReact({ page, initialProps, CONTAINER_ID }) {
  const element = React.createElement(page.view, initialProps);
  const container = document.getElementById(CONTAINER_ID);
  if (page.renderToHtml) {
    ReactDOM.hydrate(element, container);
  } else {
    ReactDOM.render(element, container);
  }
}
