import React from "react";
import page_config from "../../page_config";

export default page_config(LabelsPage, "Labels", { route: "/basic/labels" });

function LabelsPage() {
  return (
    <>
      {["Square", "Line", "Line Slim", "Badge"].map((badgeName) => {
        const bageFileName = badgeName.split(" ").join("-").toLowerCase();
        const badgeUrl =
          "https://lsos.org/basic/labels/" + bageFileName + ".svg";
        return (
          <React.Fragment key={badgeName}>
            <h2>{badgeName}</h2>
            <img src={badgeUrl} />
            <pre className="code-block">{'<img src="' + badgeUrl + '" />'}</pre>
          </React.Fragment>
        );
      })}

      <h2>More</h2>
      <p>
        We are happy to modify and create further labels. Let your Lsos Manager
        know if you have any wish.
      </p>
    </>
  );
}
