export { browserSupportsBackdropFilter };

function browserSupportsBackdropFilter() {
  const val = "saturate(180%) blur(20px)";
  const prop = "backdrop-filter";
  return (
    window.CSS &&
    window.CSS.supports &&
    (window.CSS.supports(prop, val) ||
      window.CSS.supports("-webkit-" + prop, val))
  );
}
