import logoWithTextUrl from "../logo/logo-with-text.svg";
import React from "react";
import assert from "@brillout/assert";

export { LsosLogo };

function LsosLogo({ style, height }) {
  assert(height);
  return (
    <a href="/" style={{ height, ...style }}>
      <img src={logoWithTextUrl} style={{ height }} />
    </a>
  );
}

