import React from "react";
import "./style.css";
import Header from "./Header";
import Footer from "./Footer";
import logoUrl from "./logo/logo.svg";
import assert from "@brillout/assert";
import { activate_email_links } from "./activate_email_links";

export { page_config };
export default page_config;

function page_config(
  page_view,
  page_title,
  { route, wrap, no_header, on_page_load, hydrateReact, addInitialProps } = {}
) {
  //assert(wrap===undefined);
  if (!route) {
    route = "/" + page_title.toLowerCase().split(" ").join("-");
  }

  const title = !page_title ? "Lsos" : page_title + " | Lsos";

  const view = (props) => (
    <>
      <Header />
      <div id="page-content-container">
        <div id="page-content">
          {no_header ? null : <h1>{page_title}</h1>}
          {React.createElement(page_view, props)}
        </div>
      </div>
      <Footer />
    </>
  );

  return {
    route,
    view,

    title,
    favicon: logoUrl,

    renderToDom: true,
    renderToHtml: true,
    renderHtmlAtBuildTime: true,
    hydrateReact,
    addInitialProps,
    on_page_load: () => {
      activate_email_links({ domain_name: "lsos.org" });
      on_page_load && on_page_load();
    },
  };
}
